<template>
        <div class="form-group">
            <label for="username"></label>
            <Field name="username" type="text" class="form-control" v-model="user_name" />
            <ErrorMessage name="username" class="error-feedback" />
        </div>

        <div class="form-group">
            <label for="email"></label>
            <div class="row">
                <div class="col-6">
                  <Field name="email" type="text" class="form-control" v-model="user_email" />
                  <ErrorMessage name="email" class="error-feedback" />
                </div>
                <!-- <div class="col">
                    <button class="w-100 btn btn-sm" on-click="#" type="button" id="button_regist"> </button>
                </div>     -->
                <button class="btn btn-light btn-block"  @click="FindID" id="home-tab" data-bs-toggle="tab" type="button" role="tab" aria-controls="home" aria-selected="true">
                      <p class="text-secondary"> ID </p>
                </button>
            </div>
          </div>

