<template>

  <div class="h-100 row align-items-center" id="maildetailmargin">
    <div class="bg-light p-5 rounded">
      <div class="row g-3">
        <div class="d-flex flex-row-reverse bd-highlight">
            <button type="button" @click="mainBtn" class="btn-close" data-bs-dismiss="alert" aria-label="Close">

            </button>
        </div>
        <div class="col-12">
          <p>{{ detail.title }}</p>
        </div>

        <div class="col-sm-5">
          <input type="email" class="form-control" id="Sendperson" v-bind:placeholder="detail.from" value="" required="" readonly>
          <div class="invalid-feedback">
                .
          </div>
        </div>

        <div class="col-sm-1">
          <svg xmlns="http://www.w3.org/2000/svg" width="45" height="40" fill="currentColor" class="bi bi-arrow-right"
            viewBox="0 0 16 16">
            <path fill-rule="evenodd"
              d="M1 8a.5.5 0 0 1 .5-.5h11.793l-3.147-3.146a.5.5 0 0 1 .708-.708l4 4a.5.5 0 0 1 0 .708l-4 4a.5.5 0 0 1-.708-.708L13.293 8.5H1.5A.5.5 0 0 1 1 8z" />
          </svg>
        </div>

        <div class="col-sm-5">
          <input type="eamil" class="form-control" id="Takeperson"  v-bind:placeholder="detail.to" value="" required="" readonly >
          <div class="invalid-feedback">
                .
          </div>
        </div>
        <div class="col-sm-1">

          <button v-on:click="disabled = (disabled + 1) % 2" type="submit">
            <svg xmlns="http://www.w3.org/2000/svg" width="45" height="40" fill="currentColor" class="bi bi-plus-circle"
              viewBox="0 0 16 16">
              <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z" />
              <path
                d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4z" />
            </svg>
          </button>
        </div>

        <div class="col-sm-12" v-if="disabled==1">
          <input type="text" class="form-control" id="title" placeholder="" readonly>
        </div>

        <div class="col-sm-12">
          <hr class="my-5">
        </div>
      </div>
    </div>


      <div class="col-sm-12">
        <textarea v-model="detail.message" class="form-control" id="text-area" rows="12" readonly></textarea>
      </div>
      <br><br>
    </div>
</template>

<script>
import axios from "axios";
import {object} from "yup/lib/locale"

export default {
  name : 'MailDetailPage',
  setup() {
    
  },
  props: {
    id : Number,
  },


  data() {
    return {
      disabled: 0,
      detail : object,
    }
  },  
  methods: {
    onClick(){
      this.kamzoname = true;
    },

    MailDetail(){
      axios({
        method : 'GET',
        url : '/readMail',
        params: {
          user : "test", //sessionStorage.getItem('user_id')
          boxName : "inbox",
          messageNumber : this.$route.params.id
        }
        })
        .then(response => {
          console.log(response.data);
          this.detail = response.data;
        })
        .catch(function (error) {
           console.log("");
          console.log(error);
         
        }
        );
    },
    mainBtn(){
      //window.open('./MailPage');    
      this.$router.push({path: `/mailpage`})
    },
  },
  mounted(){
  this.MailDetail();
  }
}
</script>


<style>

#maildetailmargin{
  margin-left: 70px;
}
</style>