<script>
import axios from "axios"
export default {
  name : 'MailP',
  setup() {
    
  },

  data() {
    return {
      disabled: 0,
      titletext : '',
      fromemail : sessionStorage.getItem("user_id") + '@ktds.ml',
      toemail : '',
      messages : '',
    }
  },
  methods: {
    onClick(){
      this.kamzoname = true;
    },
    

    mainBtn(){
      //window.open('./MailPage');    
      this.$router.push({path: `/mailpage`})
    },

    SendMail(){
      axios
      .post("/sendMail", {
          from : this.fromemail,
          to : this.toemail,
          // cc : this.toemail,
          subject : this.titletext,
          message : this.messages,
      })
        .then(response => {
          console.log(response)
          this.$router.push({path: `/mailpage/`})
        })
      },
  },
}
</script>
