<template>
    <!-- Modal -->
  <div class="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1"
    aria-labelledby="staticBackdropLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="staticBackdropLabel"> </h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">

          <label for="title"> </label>
          <input class="form-control" type="text" v-model="title" placeholder=" ">

          <label for="startDate"> </label>
          <input id="startDateTime" type="datetime-local" class="form-control" v-model="start_date">

          <label for="endDate"> </label>
          <input id="endDateTime" type="datetime-local" class="form-control" v-model="end_date">

          <div class="mb-3">
            <label for="memo" class="form-label"></label>
            <textarea class="form-control" id="memo" rows="3" v-model="memo"></textarea>
          </div>

        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal"></button>
          <button type="button" class="btn btn-primary" @click="TodoAdd"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
    name : "TodoModal",

    data() {
      return {
        // user_id: "sujin",
        user_id : sessionStorage.getItem('user_id'),
      }
    },

    methods: {

        // Todo  
        TodoAdd() {
            axios
                .post("/todoadd", {
                    user_id: this.user_id,
                    title: this.title,
                    start_date: this.start_date.substr(0,10),
                    end_date: this.end_date.substr(0,10),
                    memo: this.memo,

                }).then(response => {
                    if(response.status){
                        alert(" .")
                        this.$router.push("/todopage")
                    } else {
                        alert("  ")
                    }
                }).catch(err => {
                    console.log(err)
                })
        },

    },
}
</script>