<template>
    <div class="home-todo-list">

        <ul id="list-group" v-for="(todo,idx) in todos" :key="idx" style="list-style: none;">
            <li class="list-item">
                <input class="form-check-input" type="checkbox" value="todo.check_yn" aria-label="...">
                {{ todo.title }}
            </li>
        </ul>
        
    </div>
</template>

<script>
import axios from "axios";
import moment from 'moment'

export default {
    name: "todoList",
  
    data() {
        return {
            todos: [],
      
            user_id : sessionStorage.getItem('user_id'),
            // user: "sujin",

            today_date: moment(String(new Date())).format('YYYY-MM-DD'),        //  
        }
    },
    
    methods: {
        TodoToday() {
            // (user,  ) todo  
            axios
                .get(`/todo/detail?user=${this.user_id}&tododate=${this.today_date}`)
                .then(response => {
                    console.log(response.data);
                    this.todos = response.data;
                })
                .catch(e => {
                console.log(e);
                });    
        },
        
    },

    mounted() {
        this.TodoToday();
    },

}

</script>

<style scoped>

ul {
    padding-left: 0rem;
}

</style>