      this.receiveV = true
      this.sendV = false
      this.binV = false
    },

    sendBtn(){
      this.receiveV = false
      this.sendV = true
      this.binV = false
    },

    binBtn(){
      this.receiveV = false
      this.sendV = false
      this.binV = true
    },

    makeget(){
      axios({
        method : 'GET',
        url : '/readMails',
        params: {
          user : sessionStorage.getItem('user_id'),
          boxName : "inbox",
          pageNumber : this.pageNumber
        }
        })
        .then(response => {
          this.mailData = response.data;
          this.innum = this.mailData.length;
          this.countMail = response.data.messageCount;
          this.pageCount =  parseInt(this.countMail / 10 + 1);
          console.log(response.data.messageCount);
          console.log(this.mailData);

        })
        .catch(function (error) {
          console.log(error);
        }
        );
    },

    outbox(){
      axios({
